.overlay {
    display: none;
    padding: 2%;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    &__img {
        width: 80%;
        height: auto;
        margin: 0 auto;
        &_item {
            display: block;
            max-width: 100%;
            height: auto;
        }
    }
    .close {
        background: white;
        width: 20px;
        height: 20px;
        font-size: 40px;
        cursor: pointer;
    }
}