@media screen and (max-width: 1920px) {
    .overlay {
        &__img {
            width: 45%;
            height: auto;
            margin: 0 auto;
            &_item {
                display: block;
                max-width: 100%;
                height: auto;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .overlay {
        &__img {
            width: 80%;
        }
    }
}
	
@media screen and (max-width: 991px) {
    
}
	
@media screen and (max-width: 767px) {
    .portfolio__img {
        width: 48%;
    }
}
	
@media screen and (max-width: 480px) {
    .home {
        background-position: 30%;
    }
    .prices__card_container {
        flex-wrap: wrap;
        justify-content: center;
    }
    .prices__card {
        width: 80%;
        margin-bottom: 30px;
    }
    .contacts__form {
        width: 80%;
        ::placeholder{
            font-size: 12px;
        }
    }
}